<template>
  <main>
    <div
class="w-100 position-relative"
      :style="`aspect-ratio: 3; background-image: url(${data.img}); background-repeat: no-repeat; background-size: cover; background-position: center center`">
      <div
class="p-2 px-3 position-absolute bg-success"
        style="bottom: 2rem; left: 2rem; text-transform: uppercase; font-weight: 800; margin-right: 2rem">
        <h1 class="fw-bold mb-0 text-center">{{ data.title }}</h1>
      </div>
    </div>
    <b-container class="py-3" fluid>
      <b-row>
        <b-col
          cols="12"
          :sm="showSidebar ? '8' : undefined"
          :md="showSidebar ? '7' : '10'" offset-md="1"
          :xl="showSidebar ? '8' : undefined">
          <ContentRenderer :value="data">
            <ContentRendererMarkdown :value="data.body" />
          </ContentRenderer>
        </b-col>
        <b-col v-if="showSidebar" cols="12" sm="4" md="3" xl="2">
          <LinksSidebarPanel v-if="data?.links?.length > 0" class="mb-2 mb-md-3 mb-lg-4 mb-xl-5" :links="data.links" />
          <ArticleAuthorCard v-if="data.author" class="mb-3" :author="data.author" />
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script setup>
import {computed} from "vue"
import ArticleAuthorCard from "~/components/cards/ArticleAuthorCard"
import LinksSidebarPanel from "~/components/sidebars/LinksSidebarPanel"
definePageMeta({
  layout: "blog"
});

const route = useRoute();

const { data, error } = await useAsyncData(`content-${route.path}`, () => {
  return queryContent().where({ _path: route.path }).findOne();
}, {server:true});

if (error.value) console.log("Error: %o", Object.keys(error.value))

useContentHead(data)

const showSidebar = computed(() => data.value?.links?.length > 0 || data.value?.author)
</script>
